@import url(https://fonts.googleapis.com/css2?family=Hind+Siliguri:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: lightgray;
}

body {
  font-family: 'Hind Siliguri', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}
.App{text-align:left;padding-left:20px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .win-loss-section{justify-self:center;padding:40px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-section{align-self:center;justify-self:center;font-size:1.5rem}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:10px}.MatchDetailCard .match-date .match-venue .match-result{margin-top:10px}.won-card{background-color:#5ca35c}.lost-card{background-color:#d15757}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .additional-detail{text-align:right}.MatchSmallCard .additional-detail>p{margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}

.YearSelector{text-align:right;list-style-type:none}

.MatchPage{display:grid;grid-template-columns:1fr 5fr}.MatchPage .year-selector{justify-self:end;padding:40px;padding-top:85px}.MatchPage .page-heading{margin-bottom:20px;margin-top:20px}

.HomePage .app-name{font-size:3rem;margin:30px auto}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

.TeamTile{height:170px;text-align:center;border:1px solid white;padding:20px}

